html, body {
  padding: 0;
  margin: 0;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
}

.container {
  min-height: 100vh;
  max-width: 1000px;
  margin: 0 auto;
  padding: 0 1rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

input, button, p {
  margin: 0.25rem 0;
  font-size: 2rem;
}

.hidden {
  display: none;
}

a {
  color: #4592D6;
  text-decoration: none;
}

.output button {
  margin-top: 2rem;
}

footer {
  margin-top: 4rem;
}

footer p {
  font-size: 1rem;
  color: #333;
}

footer span {
  color: #999;
}

footer a {
  color: rgba(69,146,214,.8);
}